:root {
  --bg_color: #f4f4f4;
  --navbar_font_color: #444444;
}
.navbar___container {
  height: 6vw;
  padding: 0;
  margin: 0 0 0 0;
  display: flex;
  align-items: center;
  background-color: var(--bg_color);
}
.btns__container {
  display: flex;
  justify-content: right;
  background-color: var(--bg_color);
}
.logo__container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--bg_color);
}
.logo__container > img {
  width: 12vw;
  background-color: var(--bg_color);
}
.btn__container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn__container > .btn__navbar {
  background-color: var(--bg_color);
  font-family: Proxima Nova Regular;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3vw;
  cursor: pointer;
  /* border: 1px solid #013f78; */
  border: none;
  outline: none;
  transition: 250ms ease-in-out;
  color: var(--navbar_font_color);
  border-radius: 5px;
  margin: 0 1.5vw;
}
.btn__container > .btn__navbar__large {
  margin: 0 1vw;
}
.btn__container > .btn__navbar__small {
  margin: 0 3vw;
}
.btn__container > .btn__navbar:hover {
  transition: 250ms ease-in-out;
  color: #24bbec;
}
.separator {
  border-right: solid 2px var(--navbar_font_color);
}
#activate {
  color: #24bbec;
}
