:root {
  --btn_font: 2vw;
  --btn_height: 4vw;
  --head_size: 8vw;
  --head_font: 3vw;
  --body_size: 25vw;
  --form__field: 7vw;
  --form__field__label: 1.75vw;
  --form__container__size: 33vw;
  --input__size: 2.5vw;
  --input_font: 1.25vw;
  --btn_container_size: 7vw;
}

.form__container {
  font-family: Proxima Nova Ligth;
  color: rgb(119, 119, 119);
  background-color: rgb(208, 208, 208);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
}

.form__container > form {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.form__container .form__head {
  font-family: Proxima Nova Bold;
  background-color: rgb(255, 255, 255);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  /* min-height: 10vw; */
}

.form__container .form__head .form__title {
  /* font-size: 3vw; */
}

.form__container .form__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* min-height: 25vw; */
}

/*INPUTS AND LABELS*/

.form__field {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 85%;
  /* min-height: 7vw; */
}

.form__field > label {
  font-style: italic;
  /* font-size: 1.75vw; */
}

.form__field > input {
  color: rgb(119, 119, 119);
  border-radius: 25px;
  border: none;
  text-align: center;
  /* box-shadow: inset 0px 4px 7px -3px rgba(0, 0, 0, 0.1), inset 0px 23px 98px -82px rgba(0, 0, 0, 0.1); */
  /* box-shadow: inset 0px 4px 7px -3px rgba(0,0,0,0.1),inset 0px 100px 98px -82px rgba(0,0,0,0.1); */
  box-shadow: inset 0px 4px 7px -3px rgba(0, 0, 0, 0.1),
    inset 0px 100px 50px -63px rgba(0, 0, 0, 0.1);
  /* min-height: 2.5vw;
    font-size: 1.25vw; */
}

.form__field > input::placeholder {
  text-align: center;
  color: rgb(119, 119, 119);
}

.form__field > input:focus {
  border: none;
  outline: none;
}

/*BUTTONS*/
.button__container {
  display: flex;
  align-items: flex-end;
  width: 60%;
  /* min-height: 7vw; */
}

.button__container .button__login {
  border: none;
  border-radius: 35px;
  width: 100%;
  font-weight: bold;
  color: white;
  background: #106ebe;

  transition: all 200ms ease-in-out;
}
.button__login:hover {
  /*background-color: #0062cc;*/
  transform: scale(1.1);
}

/* If the screen size is 601px or more, set the font-size of <div> to 80px */

@media only screen and (max-width: 600px) {
  .form__container {
    min-height: calc(var(--form__container__size) * var(--xs));
    aspect-ratio: 15/24;
  }

  .form__container .form__head {
    min-height: calc(var(--head_size) * var(--xs));
  }

  .form__container .form__head .form__title {
    font-size: calc(var(--head_font) * var(--xs));
  }

  .form__container .form__body {
    min-height: calc(var(--body_size) * var(--xs));
  }

  .form__field {
    min-height: calc(var(--form__field) * var(--xs));
  }

  .form__field > label {
    font-size: calc(var(--form__field__label) * var(--xs));
  }

  .form__field > input {
    min-height: calc(var(--input__size) * var(--xs));
    font-size: calc(var(--input_font) * var(--xs));
  }

  .button__container {
    min-height: calc(var(--btn_container_size) * var(--xs));
  }

  .button__container .button__login {
    font-size: calc(var(--btn_font) * var(--xs));
    min-height: calc(var(--btn_height) * var(--xs));
  }
}

