@font-face {
  font-family: Proxima Nova Regular;
  src: url("./fonts/ProximaNova/Proxima\ Nova\ Regular.otf") format("opentype");
}
@font-face {
  font-family: Proxima Nova Semibold;
  src: url("./fonts/ProximaNova/Proxima\ Nova\ Semibold.otf") format("opentype");
}
@font-face {
  font-family: Proxima Nova Ligth;
  src: url("./fonts/ProximaNova/Proxima\ Nova\ Alt\ Light.otf")
    format("opentype");
}
@font-face {
  font-family: Proxima Nova Bold;
  src: url("./fonts/ProximaNova/Proxima\ Nova\ Alt\ Bold.otf")
    format("opentype");
}
@font-face {
  font-family: Proxima Nova Bold Italic;
  src: url("./fonts/ProximaNova/Proxima\ Nova\ Bold\ Italic.otf");
}
@font-face {
  font-family: Proxima Nova Semibold Italic;
  src: url("./fonts/ProximaNova/Proxima\ Nova\ Semibold\ Italic.otf")
    format("opentype");
}

* {
  /*font-family: Proxima Nova Ligth;*/
}
.nova_bold {
  font-family: Proxima Nova Bold;
}
.nova_regular {
  font-family: Proxima Nova Regular;
}
.nova_semibold {
  font-family: Proxima Nova Semibold;
}
.nova_light {
  font-family: Proxima Nova Ligth;
}

@media only screen and (max-width: 600px) {
  .tabla__reporte,
  .tabla__reporte__content,
  .table__global {
    font-size: calc(var(--tables_font_size) * 2.5);
  }
  .login__container > .img__container > img {
    height: calc(var(--logo_height) * 2.5);
  }
  .select__container {
    font-size: calc(var(--tables_font_size) * 2.5);
  }
}
@media only screen and (min-width: 600px) {
  .tabla__reporte,
  .tabla__reporte__content,
  .table__global {
    font-size: calc(var(--tables_font_size) * 2);
  }
  .login__container > .img__container > img {
    height: calc(var(--logo_height) * 2);
  }
  .select__container {
    font-size: calc(var(--tables_font_size) * 2);
  }
}
@media only screen and (min-width: 768px) {
  .tabla__reporte,
  .tabla__reporte__content,
  .table__global {
    font-size: calc(var(--tables_font_size) * 1.5);
  }
  .login__container > .img__container > img {
    height: calc(var(--logo_height) * 1.5);
  }
  .select__container {
    font-size: calc(var(--tables_font_size) * 1.5);
  }
}
@media only screen and (min-width: 992px) {
  .tabla__reporte,
  .tabla__reporte__content,
  .table__global {
    font-size: calc(var(--tables_font_size) * 1);
  }
  .login__container > .img__container > img {
    height: calc(var(--logo_height) * 1);
  }
  .select__container {
    font-size: calc(var(--tables_font_size) * 1);
  }
}
@media only screen and (min-width: 1200px) {
  .tabla__reporte,
  .tabla__reporte__content,
  .table__global {
    font-size: calc(var(--tables_font_size) * var(--xl));
  }
  .login__container > .img__container > img {
    height: calc(var(--logo_height) * var(--xl));
  }
  .select__container {
    font-size: calc(var(--tables_font_size) * var(--xl));
  }
}
