.selectGlobal__container {
  position: relative;
  font-family: Proxima Nova Semibold;
  border:none;
  display: flex;
  flex-direction: column;
  width: 100%;
}
.selectGlobal__container > .selectGlobal__head {
  color: white;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 7vh;
  background-color: #106ebe;
}
.selectGlobal__body {
  border:none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  background-color: #c3c5c5;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 10vh;
}
.selectGlobal__body > select {
  color: #000000;
  text-align: center;
  width: 55%;
  height: 35%;
  -webkit-appearance: button;
  -moz-appearance: button;
  background: #ffffff;
  -webkit-user-select: none;
  -moz-user-select: none;
  border-radius: 9px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 1vw;
}
.selectGlobal__body > select > option {
  font-family: Proxima Nova Regular;
}
.selectGlobal__body > select:focus {
  outline: none;
}
