:root{
  --tables_font_size: 1vw;
}
.table__global {
  width: 100%;
  font-family: Proxima Nova Ligth;
}
.table__global > thead > tr > th {
  color: white;
  background-color: #106ebe;
  border: solid 2px rgba(255, 255, 255, 0.83);
  border-radius: 8px;
  height: 7vh;
  min-width: 8vw;
  font-weight: normal;
  font-family: Proxima Nova Regular;
  padding: 0 2vw;
  font-size: 1vw;
}
.table__global > tbody > tr > td {
  font-size: 1vw;
  height: 5vh;
  font-family: Proxima Nova Regular ;
  background-color: #c3c5c5;
  border: solid 2px rgba(255, 255, 255, 0.83);
  border-radius: 8px;
  color: rgb(0, 19, 20);
  padding: 0 2vw;
}
#tdTotal{
  background-color: #106ebe;
  color: aliceblue;
}