:root {
  --title_selectors_font_size: 1.2vw;
  --dashboardSelectors-font-color: #f4f4f4;
}
.dashboardPage__container {
  background-color: var(--bg_color);
}
.dashboard_selectors {
  margin: 2vh 0 0 2vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #106ebe;
  height: 8vh;
}
.dashboard_selectors >label {
  margin: 0 0 0 4vw;
  color: #ffffff;
  font-family: Proxima Nova Regular;
  font-size: 1.5vw;
}
.dashboard_selectors > .dashboarpage__btn {
  font-family: Proxima Nova Regular;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 1.1vw;
  border: none;
  background-color: #106ebe;
  transition: 250ms ease-in-out;
  color: var(--dashboardSelectors-font-color);
  height: 75%;
  padding: 0 2vw 0 2vw;
}
.dashboard_selectors > .dashboarpage__btn1 {
  margin-left: 4vw;
  border-right: solid 2px var(--dashboardSelectors-font-color);
}
.dashboard_selectors > .dashboarpage__btn:hover {
  color: #24BBEC;
  transition: 250ms ease-in-out;
}

.dashboardPage__title {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #292960;
}
.selects__container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 3vh 0;
}
.dashboard__container {
  font-family: Proxima Nova Semibold;
}
.dashboarpage__calcular__btn__container{
  display: flex;
  justify-content: center;
  align-items: center;
}
.dashboarpage__calcular__btn__container .dashboarpage__calcular__btn{
  font-size: 1.2vw;
  border: none;
  background-color: #106ebe;
  border-radius: 5px;
  width: 20vw;
  aspect-ratio: 6/1;
  transition: all 200ms ease-in-out;
  color: #ffffff;
  font-family: Proxima Nova Semibold;
}
.dashboarpage__calcular__btn__container .dashboarpage__calcular__btn:hover{
  transform: scale(1.1);
}
.path__container {
  width: 100%;
  margin: 3vh 0 0vh 2vw;
  display: flex;
  justify-content: left;
  align-items: center;
}
.btn__atras{
  font-size: 1.2vw;
  font-family: Proxima Nova Regular;
  color:white;
  border: none;
  background-color: #106ebe;
  border-radius: 5px;
  width: 10vw;
  aspect-ratio: 4/1;
}
@media only screen and (max-width: 600px) {
  .selects__container {
    font-size: calc(var(--title_selectors_font_size));
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .selects__container {
    font-size: calc(var(--title_selectors_font_size));
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .selects__container {
    font-size: calc(var(--title_selectors_font_size));
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .selects__container {
    font-size: calc(var(--title_selectors_font_size));
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .selects__container {
    font-size: calc(var(--title_selectors_font_size));
  }
}
