.selectGlobal__container {
  position: relative;
  -webkit-border-top-left-radius: 20px;
  -webkit-border-top-right-radius: 20px;
  -moz-border-radius-topleft: 20px;
  -moz-border-radius-topright: 20px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  font-family: Proxima Nova Semibold;
  display: flex;
  flex-direction: column;
  width: 100%;
}
.selectGlobal__container > .selectGlobal__head {
  color: white;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  background-color: #292960;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3vw;
}
.selectGlobal__body {
  background-color: #24bbec;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 5vw;
}
.selectGlobal__body > select {
  color: white;
  text-align: center;
  font-size: 1vw;
  width: 60%;
  height: 40%;
  -webkit-appearance: button;
  -moz-appearance: button;
  background: #292960e0;
  -webkit-user-select: none;
  -moz-user-select: none;
  /*
      background-image: url(https://stackoverflow.com/favicon.ico), -webkit-linear-gradient(top, #E5E5E5, #F4F4F4); 
      background-image: url(https://stackoverflow.com/favicon.ico), -moz-linear-gradient(top, #E5E5E5, #F4F4F4);  
      background-image: url(https://stackoverflow.com/favicon.ico), -ms-linear-gradient(top, #E5E5E5, #F4F4F4); 
      background-image: url(https://stackoverflow.com/favicon.ico), -o-linear-gradient(top, #E5E5E5, #F4F4F4);  
      background-image: url(https://stackoverflow.com/favicon.ico), linear-gradient(to bottom, #E5E5E5, #F4F4F4);  
      background-position: center right;
      background-repeat: no-repeat;
      border: 1px solid #aaa;
      */
  border-radius: 9px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.selectGlobal__body > select:focus {
  outline: none;
}
