:root{
    --title_graphs_size:1.2vw;
}

.dashboard__container > .row1,
.dashboard__container > .row2,
.dashboard__container > .row3
{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 0;
}
.dashboard__container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.dashboard__container > .row2{
    display: flex;
    justify-content: space-around;
    width: 95%;
}
.dashboard__container > .row2 > .piechart__container{
    width: 49%;
}
.linechart__container,.piechart__container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    aspect-ratio: 2/1;
    border: solid 2px #292960;
    border-radius: 5px;
    position: relative;
    font-family: Proxima Nova Semibold;
    margin: 3vh 0;
}
.piechart__container{
    aspect-ratio: 2/1.3;
    
}
.pie__title__container,.line__title__container{
    position: absolute;
    text-align: center;
    width: 70%;
    background-color: #106ebe;
    border-radius: 5px;
    color: white;
    top: -3%;
}
.btn__atras__path{
    border: none;

    border-radius: 5px;
}
@media only screen and (max-width: 600px) {
    .pie__title__container,.line__title__container{
        font-size: calc( var(--title_graphs_size))
    }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
    .pie__title__container,.line__title__container{
        font-size: calc( var(--title_graphs_size))
    }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
    .pie__title__container,.line__title__container{
        font-size: calc( var(--title_graphs_size))
    }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
    .pie__title__container,.line__title__container{
        font-size: calc( var(--title_graphs_size))
    }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
    .pie__title__container,.line__title__container{
        font-size: calc( var(--title_graphs_size))
    }
}
